import * as React from 'react';
import './NavBarWithDrawer.css';
import { Box, SwipeableDrawer, Button, List, ListItem, ListItemButton, ListItemIcon, ListItemText } from '@mui/material'
// // import Divider from '@mui/material/Divider';
// import InboxIcon from '@mui/icons-material/MoveToInbox';
// import MailIcon from '@mui/icons-material/Mail';
import MenuIcon from '@mui/icons-material/Menu';
import { ShoppingCart, RestaurantMenu, Home, Add } from '@mui/icons-material';
import { Link } from 'react-router-dom';

export default function NavBarWithDrawer({ title, listType, addToListFunction }) {
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  function onAddFunction() {
    addToListFunction();
  }

  const navMenuName = [
    { id: 'pantry', text: 'Pantry', icon: <Home /> },
    { id: 'shoppinglist', text: 'Shopping List', icon: <ShoppingCart /> },
    { id: 'recipes', text: 'Recipes', icon: <RestaurantMenu /> }
  ];

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <Box
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {navMenuName.map((navMenuObj, index) => (
          <ListItem key={navMenuObj.id} disablePadding>
            <Link to={'/' + navMenuObj.id}>
              <ListItemButton>
                <ListItemIcon>
                  {navMenuObj.icon}
                </ListItemIcon>
                <ListItemText primary={navMenuObj.text} />
              </ListItemButton>
            </Link>
          </ListItem>
        ))}

      </List>
      {/* <Divider />
      <List>
        {['All mail', 'Trash', 'Spam'].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List> */}
    </Box>
  );

  return (
    <div className='header'>
      {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}><MenuIcon /></Button>
          <h1>{title}</h1>
          <Button><Add onClick={onAddFunction} /></Button>
          <SwipeableDrawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list(anchor)}
          </SwipeableDrawer>
        </React.Fragment>
      ))
      }
    </div >
  );
}
