import React from 'react'
import { pantryListData } from '../model/PantryListData';
import { shoppingListListData } from '../model/ShoppingListListData';
import { recipesListData } from '../model/RecipesListData';

export default class ListManagementController extends React.Component {
    // constructor(props) {
    //     super(props);
    // }

    AddItemToList({ listType, item = null }) {
        console.log('listtype: ' + listType);
        console.log('item: ' + item.title + ' ' + item.subtitle);
        switch (listType) {
            case 'Pantry List':
                if (item != null) {
                    pantryListData.push({ title: item.title, subtitle: item.subtitle });
                }
                return 'Success';
            case 'Shopping List':
                return shoppingListListData;
            case 'Recipes List':
                return recipesListData;
            default:
                return 'Failed';
        }
    }

    GetList(listType) {
        switch (listType) {
            case 'Pantry List':
                return pantryListData;
            case 'Shopping List':
                return shoppingListListData;
            case 'Recipes List':
                return recipesListData;
            default:
                return [];
        }
    }
}

