import React, { useEffect, useState } from 'react'
import List from '../components/List'
import ListManagementController from '../../controller/ListManagementController.js'
import NavBarWithDrawer from '../components/NavBarWithDrawer.js';

const Pantry = () => {
    const listTypeValue = 'Pantry List';
    const lmc = new ListManagementController();

    const [pantryList, setPantryList] = useState(lmc.GetList(listTypeValue));

    const addFunction = () => {
        console.log('pantry list before: ' + pantryList);
        var tempObject = { title: 'New title', subtitle: 'new subtitle' };

        let result = lmc.AddItemToList({ listType: listTypeValue, item: tempObject });
        console.log(result);
        console.log('pantry list after: ' + pantryList);

        setPantryList(lmc.GetList(listTypeValue));
    }

    return (
        <div>
            <NavBarWithDrawer title='Pantry' addToListFunction={addFunction} />
            <List listToBeRendered={pantryList} />
        </div>
    )
}

export default Pantry