import { Box } from '@mui/material'

import ListMUI from '@mui/material/List'
import ListItem from './ListItem.js'
import React from 'react'
import './List.css'

const List = ({ listToBeRendered }) => {
    return (
        <Box className='list'>
            <nav aria-label='mainlist'>
                <ListMUI>
                    {/* <ListItem /> */}
                    {listToBeRendered.map((listObj, index) =>
                        <ListItem key={index} title={listObj.title} subtitle={listObj.subtitle} />
                    )}
                </ListMUI>
            </nav>
        </Box>
    )
}

export default List