import React from 'react'
import List from '../components/List'
import ListManagementController from '../../controller/ListManagementController.js'
import NavBarWithDrawer from '../components/NavBarWithDrawer.js';

const ShoppingList = () => {

    const lmc = new ListManagementController();

    return (
        <div>
            <NavBarWithDrawer title='Shopping List' />
            <List listToBeRendered={lmc.GetList('Shopping List')} />
        </div>
    )
}

export default ShoppingList